<template>
  <div id="add-blog">
    <h2>添加博客</h2>
    <form v-if="!submited">
      <label >标题:</label>
      <input type="text" v-model="blog.title">
      <label >内容:</label>
      <textarea v-model="blog.content" ></textarea>
      <div id="checkboxes">
        <label >vue.js</label>
        <input type="checkbox" value="HTML" v-model="blog.categories">
        <label >html</label>
        <input type="checkbox" value="Vue.js" v-model="blog.categories">
        <label >css</label>
        <input type="checkbox" value="CSS" v-model="blog.categories">
        <label >JavaScript</label>
        <input type="checkbox" value="JavaScript" v-model="blog.categories">
        <label >Others</label>
        <input type="checkbox" value="Others" v-model="blog.categories">
      </div>
      <label >作者:</label>
      <select v-model="blog.author">
        <option v-for="author in authors" :key="author">
          {{ author}}
        </option>
      </select>
      <p>作者: {{ blog.author }}</p>
    <button @click.prevent="post">提交博客</button>
    <hr>
    </form>
    

    <div id="show">
      <P>标题:</P>
      <p>{{ blog.title}}</p>
      <p>内容:</p>
      <p> {{ blog.content}}</p>
      
      <p>博客分类:</p>
      <p v-for="category in blog.categories" :key="category">{{ category }}</p>
      <p>作者:</p>
      <p>{{ blog.author }}</p>
    </div>
    
  </div>
</template>

<script>
export default {
  name: 'add-blog',
  props: {
    msg: String
  },
  data(){
    return{
      blog:{
        title:"",
        content:"",
        categories:[],
        author:"",

      },
      authors:["Dora","Moxi","Gumiao","Limeng"],
      submited:false,//用来控制提交博客不展示表单
    }  
  },
  methods:{
    post(){
      this.$http.post('http://jsonplaceholder.typicode.com/posts',{
        title:this.blog.title,
        body:this.blog.content,
        userId:1
      })
      .then(function(data){
        console.log(data),
        this.submited = true
        document.write('博客提交成功')
      });
    }
  }
  
}
</script>


<style  scoped >
#add-blog *{
  box-sizing: border-box;
}
#add-blog{
  margin: 20px auto;
  max-width: 600px;
  padding:20px
}
#add-blog h2{
  text-align: center;
}
label{
  display: block;
  margin: 20px 0 10px;
}
input[type="text"],textarea,select{
  display: block;
  width: 100%;
  padding: 8px;

}
textarea{
  height: 200px;
}
#checkboxes label{
  display: inline-block;
  margin-right: 10px;
}
#checkboxes input{
  display: inline-block;
  margin-right: 10px;
}
button{
  display: block;
  margin: 20px 0;
  background: coral;
  border: 0;
  padding: 14px;
  border-radius: 4px;
  font-size: 18px;
  cursor: pointer;
}
#show{
  padding: 10px 20px;
  border: 1px dotted #ccc;
  margin: 30px 0;
}
h3{
  margin-top: 10px;
}
</style>
