<template>
  <div id="show-blogs">
     <h1>博客总览</h1>
     <input v-model="search" placeholder="搜索">
     <div class="singleBlog" v-for="blog in filtereBlogs" :key="blog.index">
       <router-link :to="'/bolg/'+blog.id">
         <h2>{{ blog.title | toUppercase }}</h2>
         </router-link>
       <article>{{ blog.body | sipnit }}</article>
     </div>
  </div>
</template>

<script>


export default {
  name: 'show-blogs',
  data(){
    return{
      blogs:[],
      search:""
    }   
  },
  created(){
    this.$http.get('http://jsonplaceholder.typicode.com/posts')
    .then(function(data){
      this.blogs = data.body.slice(0,10)//用来规定只展示十条数据
    })
  },
  computed:{
    filtereBlogs:function(){
      return this.blogs.filter((blog) => {
        return blog.title.match(this.search)
      })
    }
  },
  filters:{
    toUppercase(value){
      return value.toUpperCase();
    },
    sipnit(value){
      return value.slice(0,100)+"...";
    }   
  } 
}


</script>

<style>
#show-blogs{
  max-width: 800px;
  margin: 0 auto;
  position: relative;
}
#show-blogs h1{
  text-align: center;
}
#show-blogs input{
  position: absolute;
  top: 30px;
  right: 0;
}
.singleBlog{
  padding: 20px;
  margin: 20px 0;
  box-sizing: border-box;
  background: rgb(235, 224, 224);
  border: 1px dotted #aaa;
}
#show-blogs a{
  color:#444;
  text-decoration: none;
}
input[type="input"]{
  padding: 8px;
  width: 100%;
  box-sizing: border-box;
}
</style>
